import { Component, Sanitizer } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { DomSanitizer } from '@angular/platform-browser';

/**
 * Generated class for the MPlayPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name:'play',
  segment:'play/:movie_id/:episode_id'
})

@Component({
  selector: 'page-m-play',
  templateUrl: 'm-play.html',
})
export class MPlayPage {
  public movie_id :number;
  public episode_id : string;
  public flim:any;
  public list_server:any;
  public list_episode:any;
  public episodeP:string;
  public iframe:any;
  constructor(public navCtrl: NavController,
    public api:ApiProvider,
    public sanitization:DomSanitizer,
    public navParams: NavParams) {
    this.movie_id = this.navParams.get('movie_id');
    this.episode_id = this.navParams.get('episode_id');
  }

  async ionViewDidLoad() {
    console.log(this.movie_id,this.episode_id);
    this.episodeP = this.episode_id;
    let res:any = await this.api.get_episode_flim(this.movie_id,this.episode_id);
    console.log(res);
    this.flim = res.flim[0];
    this.list_server = JSON.parse(res.flim[0].servers);
    this.load_iframe(this.list_server.server1);
    this.list_episode = res.list_episode;
  }
  load_iframe(link){
    if(link.indexOf(".m3u8") != -1){
      this.iframe = this.sanitization.bypassSecurityTrustHtml(`<video src=${link} autoplay=true controls height='250px' width='100%'></video>`)
    } else {
      this.iframe = this.sanitization.bypassSecurityTrustHtml(`<iframe  sandbox=" allow-scripts allow-same-origin" class="embed-responsive-item" 
     src=${link} allowfullscreen="true" scrolling="no" width="100%" height="250px"></iframe>`)
    }
    
   }

   push_play(episode){
    this.navParams.data.episode_id=episode
      this.ionViewDidLoad();
   }
}



